import React, { Component } from 'react'
import axios from 'axios'

//Global_variable for RESTAPI_URL
const url = `http://localhost:3001/users` //array type of object

class Restapi extends Component {
    constructor(props) {
        super(props)
        this.state =
        {
            userData: [] //empty Array
        }
    }
    //Render_is_Mounting_Method
    render() {
        return (<>
            <div className="container p-5">
                <h3 className="text-center"> FETCH RESTAPI DATA USING LOCAL URL/CLIENT</h3>
                <table className="table table-success table-border">
                    <thead className="table table-primary">
                        <tr>
                            <th>ID</th>
                            <th>NAME</th>
                            <th>EMAIL</th>
                            <th>PHONE</th>
                            <th colSpan="4" className="text-center"> ACTION</th>
                        </tr>
                    </thead>

                    <tbody>
                        {
                            this.state.userData.map((res, i) => {
                                return (<tr key={i}>
                                    <td>{res.id}</td>
                                    <td>{res.name}</td>
                                    <td>{res.email}</td>
                                    <td>{res.phoneno}</td>
                                    <td>{res.action}</td>
                                    <td>
                                        <button className="btn btn-primary">view</button>
                                    </td>
                                    <td>
                                        <button className="btn btn-warning">Edit</button>
                                    </td>
                                    <td>
                                        <button className="btn btn-danger">Delete</button>
                                    </td>
                                </tr>)
                            })
                        }
                
                    </tbody>
                 
                </table>

            </div>
        </>)

      
      }
         //RESTAPI_CALLS
         componentDidMount(){
            axios.get(url).then((result) => this.setState({ userData:result.data }))
         }
    }
export default Restapi